.animal-show{
    position: relative;
    border: 1px solid lightgray;
    padding: 10px;
    border-radius: 5px;
    margin: 10px;
    box-shadow: 0px 3px 4px lightgray;
}
.animal{
    height: 200px;
}
.heart{
    position: absolute;
    right: 10%;
    bottom: 10%;
    max-width: fit-content;
}

